export default function(state={}, action){
    switch(action.type){
        case('GET_BOOKS'):
            return {...state, list:action.payload}
        case('GET_BOOK_W_REVIEWER'):
            return {
                ...state,
                book: action.payload.book,
                reviewer: action.payload.reviewer
            }
        case('CLEAR_BOOK_W_REVIEWER'):
        return {
            ...state,
                book: action.payload.book,
                reviewer: action.payload.reviewer
        }
        case('ADD_BOOK'):
        return {
            ...state,
                newbook: action.payload
        }
        case('CLEAR_NEW_BOOK'):
        return {
            ...state,
                newbook: action.payload
        }
        case('GET_BOOK'):
        return {
            ...state,
                bookdata: action.payload
        }
        case('UPDATE_BOOK'):
        return {
            ...state,
            successBook:action.payload.success,
            bookdata:action.payload.doc

        }
        case('DELETE_BOOK'):
        return {
            ...state,
            postDeleted:action.payload

        }
        case('CLEAR_BOOK'):
        return {
            ...state,
            successBook:action.payload.successBook,
            book:action.payload.book,
            postDeleted:action.payload.postDeleted

        }
        default:
            return state;
    }
}


